.contact {

  &__info {
    margin: 0 0 24px;
  }

  &__info-title {
    margin: 0 0 8px;
    font-size: 14px;
    line-height: 27px;
    color: #9fadc2;
  }

  &__info-link {
    font-size: 16px;
    line-height: 27px;
    color: #3e3e3e;
    transition: color 0.5s;

    &:hover {
      color: $colorBlue;
    }
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }
}
